.redes {
    margin: 20px;
}
.redes__section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding-top: 80px;
}
.redes__section-title {
    color: var(--first-color);
    font-size: 20px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    width: fit-content;
    text-align: center;
    padding-right: 10px;
}
.redes__section-box {
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.redes__section-box-icon {
    font-size: 80px;
}

@media (max-width:850px){
    .redes__section-box {
        flex-direction: row;
        gap: 30px;
        flex-wrap: wrap;
        justify-content: center;
    }
}




